@charset "utf-8";
.webmain .con { margin-bottom: 45px; }
.webmain .con .title { height: 60px; overflow: hidden; background: url('../../../images/line_contitle.gif') center center repeat-x; margin-bottom: 15px; position: relative; }
.webmain .con .title a.more { display: block; position: absolute; right: 0; top: 16px; background: url('../../../images/more_contitle.gif'); width: 130px; height: 30px; text-indent: -99em; overflow: hidden; }
.webmain .con .title h2 { display: block; width: 200px; height: 60px; background-image: url('../../../images/sprite_active.jpg'); background-repeat: no-repeat; text-indent: -99em; overflow: hidden; float: left; }
.webmain .con .title ul { float: left; height: 26px; background: #fff; padding: 17px 5px 17px 5px; }
.webmain .con .title ul li { float: left; font-size: 16px; height: 26px; line-height: 26px; overflow: hidden; margin-right: 8px; }
.webmain .con .title ul li a { display: block; width: 100%; height: 26px; line-height: 26px; cursor: pointer; padding: 0 8px; }
.webmain .con .title ul li.c a { background: #000; color: #fff; }
.webmain .conTopbanner { }
.webmain .conTopbanner { height: 470px; overflow: hidden; }
.webmain .conTopbanner img { display: block; width: 100%; height: 470px; }

/*导航栏*/
.navTitle { color: #ccc; margin-bottom: 30px; }
.navTitle a { color: inherit }
.navTitle a:hover { color: red}
.webmain .topCon { }
.webmain .topCon .header{margin-bottom: 30px;}
.webmain .topCon .header h1{font-weight: bold; font-size: 25px;display: inline-block}
.webmain .topCon .header .newActive { float: right; width: 155px; height: 40px; margin: 0 20px 0 15px; text-align: center; line-height: 40px; color: #fff;background-color: #000;}
.webmain .topCon p{font-size: 12px; line-height: 30px;color: #ccc}
.webmain .topCon p em{color: #000}

.webmain .content{margin-bottom: 40px;}
.webmain .content img{margin: 40px auto}
.webmain .content p{width: 1000px;margin: auto; line-height: 30px;}

.webmain .pic h2{background-position: 0 -60px;}
.webmain .pic .inner{margin-left: -30px; width: 1230px;}
.webmain .pic .inner .item{float: left; margin-left: 30px;}


.webmain .fav h2{background-position: 0 -120px;}


.wrapper { position: relative; width: 100%; height: 100%; }
.mask { position: absolute; top: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, .34); display: none }
.mask .popbox { position: absolute; top: 0; bottom: 0; left: 0; right: 0; width: 575px; height: 775px; margin: auto; background-color: #fff; border: 5px solid #666; }
.mask .popbox h3 { font-size: 16px; line-height: 50px; text-indent: 20px; }
.mask .popbox input { margin-left: 30px; margin-bottom: 24px; height: 35px; text-indent: 15px; background-color: #fff; border: 1px solid #ccc; }
.mask .popbox textarea { width: 500px; height: 108px; margin-left: 30px; margin-bottom: 5px; text-indent: 15px; font-size: 14px; line-height: 30px; background-color: #fff; border: 1px solid #ccc; overflow-y: hidden }
.mask .popbox select { width: 118px; height: 35px; text-indent: 15px; color: #999; background-color: #fff; }
.mask .popbox select option { text-indent: 15px }
.mask .popbox .pic { width: 500px; }
.mask .popbox .camera { display: inline-block; position: absolute; width: 35px; height: 35px; margin-left: -40px; margin-top: 1px; background: url("../../../images/icon_camera.jpg") 0 0 no-repeat; cursor: pointer }
.mask .popbox .activeName { width: 500px }
.mask .popbox .activeAdress { margin-bottom: 0; border: 0; margin-left: 0 }
.mask .popbox .border { margin-left: 30px; border: 1px solid #ccc; margin-bottom: 24px }
.mask .popbox .border { width: 500px }
.mask .popbox .border:before { content: ""; border-right: 1px solid #ccc; height: 20px; position: absolute; margin-left: 115px; margin-top: 10px; }
.mask .popbox .fromTime { width: 240px }
.mask .popbox .toTime { margin-left: 0; width: 240px }
.mask .popbox .submit { position: absolute; width: 245px; margin-left: 155px; background-color: #000; color: #fff }
